import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import {Controller, useForm} from "react-hook-form";
import {ISignInFormData} from "../../../../interfaces/Producer";
import {connect} from "react-redux";
import {signInProducer} from "../../../../state/actions";
import {useHistory} from "react-router-dom";


const useStyles = makeStyles(theme => ({
    paper: {
        marginTop: theme.spacing(2),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        // width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

interface Props {
    children?: React.ReactElement;
    signInProducer: Function;

}


const _SignInForm = (props: Props) => {
    const classes = useStyles();
    const {handleSubmit, control} = useForm<ISignInFormData>();
    const history = useHistory();


    const onSubmit = handleSubmit(
        ({
             producerContractAddress
         }) => {
            props.signInProducer(
                producerContractAddress,
                history
            );
        });


    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline/>
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    <LockOutlinedIcon/>
                </Avatar>
                <Typography component="h1" variant="h5">
                    Sign in
                </Typography>
                <form className={classes.form} onSubmit={onSubmit}>
                    <Controller
                        name="producerContractAddress"
                        as={<TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="producerContractAddress"
                            label="Producer Contract Address"
                            name="producerContractAddress"
                            autoFocus
                        />}
                        control={control}
                        defaultValue="0xb82BC4B22A092C8C80AA04170482AfF3189FCD30"
                    />

                    <FormControlLabel
                        control={<Checkbox value="remember" color="primary"/>}
                        label="Remember me"
                    />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Sign In
                    </Button>
                </form>
            </div>
        </Container>
    );
};


export const SignInForm = connect(
    null,
    {signInProducer}
)(_SignInForm);
